import { createWebHistory, createRouter } from "vue-router";

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: ()=> import('@/pages/HomePage') // 확장자 vue는 생략해도 동작함
  },
  {
    path: "/Profile",
    name: "ProfilePage",
    component: () => import('@/pages/ProfilePage')
  },
  {
    path: "/Skill",
    name: "SkillPage",
    component: () => import('@/pages/SkillPage')
  },
  {
    path: "/Portfolio",
    name: "PortfolioPage",
    component: () => import('@/pages/PortfolioPage')
  },


  // 강제주소 변경시 404페이지로 강제 이동
  // {
  //   path: "/:pathMatch(.*)*",
  //   redirect: "/404"
  // },
  // {
  //   path: "/404",
  //   component: ()=> import('@/pages/NotPage')
  // }
]
const router = createRouter ({
  history: createWebHistory(),
  routes
})

export default router;