<template>
  <!-- @dark는 emit가 있던 곳에서 작명한 이름과 같아야함 -->
  <!-- <NavMenu @dark="event => DarkMode()" /> -->
    <!-- {{ isDark }} -->
  <!-- 로컬 스토리지에 저장이되서 새로고침해도 다크모드가 풀리지 않음 -->
  <!-- <FontBox  :langList="langList[lang]" /> -->

  <NavMenu @MenuIndex="MenuIndex = $event;" @click="SectionMove()" @dark="event => toggleDark()" :isDark= "isDark" @lang="lang=$event" :langList="langList[lang]" />

    <!-- 현재 클릭한 네비 MenuIndex 클릭 하면 Nav.vue 로 이벤트 진행 하고 그리고 App.vue 에 $event 에서 그 위치 정보를 받고 이 정보는 MenuIndex 로 이동한다
    즉 다시 말해서 MenuIndex 를 바꾸기 위해 이래저래 왕복한 경우 이다.
  순서 App.vue >> @MenuIndex 이벤트 발생  >> Nav.vue 로 이동 >> button  $emit 으로 이동해서 작업 수행 >> App.vue 에서 $event 에서 정보를 받는다
>> MenuIndex 에 정보가 저장된다. -->
<div class="fixed">

</div>    

    <div ref="메인페이지">
      <Home :langList="langList[0].MainTxt[0]" />
    </div>
  <div ref="1페이지">
    <Profile  />
  </div>
  <div ref="2페이지">
    <Skill  />
  </div>
  <div ref="3페이지">
    <Portfolio  />
  </div>
 <!-- <router-view /> -->
 <Footer />
</template>
<script>
// import로 불러와서 components와 템플릿 영역에 집어넣기
import Language from './assets/Language.json'
import Home from './pages/HomePage.vue'
import Profile from './pages/ProfilePage.vue'
import Skill from './pages/SkillPage.vue'
import Portfolio from './pages/PortfolioPage.vue'
// import FontBox from './components/FontBox.vue'
import Footer from './components/Footer.vue'
import NavMenu from './components/Nav.vue'
import {useDark, useToggle} from '@vueuse/core';
export default {
  name:'App',
  components: {
    Home,
    Profile,
    Skill,
    Portfolio,
    NavMenu,
    Footer,
    // FontBox
  },
  data() {
    return {
      lang:0,
      isDark: useDark(),
      toggleDark : useToggle(useDark()),
      FontStyle: "",
      MenuIndex: '메인페이지',
      ArrayList: '',
      langList:Language
    }
  },
  methods: {
    DarkMode(){
      // 토글 클래스
      document.querySelector("html").classList.toggle('dark')
    },
    SectionMove(){
      // getBoundingClientRect - 엘리먼트(요소)의 크기와 뷰포트에 상대적인 위치에 대한 정보 제공 하는 객체를 반환
      // this.ArrayList[this.MenuIndex].getBoundingClientRect is not a function()
      const rect = this.ArrayList[this.MenuIndex].getBoundingClientRect();
      // console.log(rect)
      const offset = rect.top + window.scrollY-60;
      window.scrollTo({
        top: offset,
        behavior:'smooth'
      })
      // scrollTo - 자바스크립트 기본 메소드 이며, 페이지의 특정 위치로 스크롤이 가능
      // window.scrollTo(x좌표, y좌표,행동)
      // behavior - 스크롤동작을 지정 "auto" 기본값 "smooth" 부드러운 에니메이션과 함께 스크롤
    }
  },
  mounted(){
    // 선택한 값이 로컬 스토리지에 저장되는 코드 - 자주 쓰임
    this.FontStyle = localStorage.getItem("font");
    this.lang = localStorage.getItem("language")
    // 화면이 로딩되면 lang값을 스토리지의language의 값으로 변경한다.
    document.querySelector("body").classList.add(this.FontStyle)
    this.ArrayList = this.$refs;
    // ref에 s를 붙이는건 기본 문법
    // console.log(this.$refs)
  },
}
</script>
